import React, {useEffect, useState} from "react";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography"
import FormControl from "@mui/material/FormControl"
import InputLabel from "@mui/material/InputLabel"
import Select from "@mui/material/Select"
import MenuItem from "@mui/material/MenuItem"
import Stack from "@mui/material/Stack"
import TextField from "@mui/material/TextField"
import FormControlLabel from "@mui/material/FormControlLabel"
import Checkbox from "@mui/material/Checkbox"
import RadioGroup from "@mui/material/RadioGroup"
import Radio from "@mui/material/Radio"
import Button from "@mui/material/Button"

function AddRentPage(props) {
    const {cityData} = props;
    //
    const [floorStatus, setFloorStatus] = useState(false); //
    const [addressCity, setAddressCity] = useState(""); //
    const [addressArea, setAddressArea] = useState(""); //
    const [Address, setAddress] = useState("") //
    const [houseType, setHouseType] = useState(""); //
    const [roomType, setRoomType] = useState(""); //
    const [squareFeet, setSquareFeet] = useState(); //
    const [rentMoney, setRentMoney] = useState(); //
    const [deposit, setDeposit] = useState(""); //
    const [rooms, setRooms] = useState([
        {id: 1, roomName: "", roomNumber: 0},
        {id: 2, roomName: "", roomNumber: 0},
        {id: 3, roomName: "", roomNumber: 0},
        {id: 4, roomName: "", roomNumber: 0},
    ]); //
    const [ShortestRentTime, setShortestRentTime] = useState(""); //
    const [SexRequirement, setSexRequirement] = useState(""); // 
    const [ableToFire, setAbleToFire] = useState(false); // 
    const [AblePet, setAblePet] = useState(false); // 
    const [ableToGetInDate, setAbleToGetInDate] = useState(""); //
    const [Describe, setDescribe] = useState("") //
    const [Title, setTitle] = useState("") //
    const [HouseForward, setHouseForward] = useState("") //
    const [images, setImages] = useState([]); //
    const [ImgURL, setImgURL] = useState([]); //
    const [furnitureArray, setFurnitureArray] = useState([ //
        {id: 1, furniture: "", status: false},
        {id: 2, furniture: "", status: false},
        {id: 3, furniture: "", status: false},
        {id: 4, Furniture: "", status: false},
        {id: 5, furniture: "", status: false},
        {id: 6, furniture: "", status: false},
    ]);
    const [ServiceArray, setServiceArray] = useState([ //
        {id: 1, Service: "", status: false},
        {id: 2, Service: "", status: false},
        {id: 3, Service: "", status: false},
        {id: 4, Service: "", status: false},
        {id: 5, Service: "", status: false},
        {id: 6, Service: "", status: false},
    ]);
    const [PublicFacilityArray, setPublicFacilityArray] = useState([ //
        {id: 1, PublicFacility: "", status: false},
        {id: 2, PublicFacility: "", status: false},
    ]);
    const [IdentifyRequirement, setIdentifyRequirement] = useState([ // 
        {id: 1, Identify: "", status: false},
        {id: 2, Identify: "", status: false},
        {id: 3, Identify: "", status: false},
    ]);
    const [rentMoneyHaveArray, setRentMoneyHaveArray] = useState([ //
        {id: 1, rentMoneyItem: "", status: false},
        {id: 2, rentMoneyItem: "", status: false},
        {id: 3, rentMoneyItem: "", status: false},
        {id: 4, rentMoneyItem: "", status: false},
        {id: 5, rentMoneyItem: "", status: false},
        {id: 6, rentMoneyItem: "", status: false},
        {id: 7, rentMoneyItem: "", status: false},
    ]);

    const CountryArray = Object.keys(cityData);

    const handleRoomType = e => { //
        setRoomType(e.target.value);
        if (e.target.value !== "") {
            setFloorStatus(true);
        } else {
            setFloorStatus(false);
        }
    }

    const handleSetRoom = (inputRoomNumber, roomName) => {
        const newRooms = rooms.map((room) => ({
            ...room,
            roomNumber: room.roomName === roomName ? inputRoomNumber : room.roomNumber
        }));
        setRooms(newRooms);
    }

    const handleChangeArray = (ItemID, setArrayFunction, setArrayName) => { //CheckBox
        setArrayFunction(setArrayName.map((Item) => ({
            ...Item,
            status: Item.id === ItemID ? !Item.status : Item.status
        })))
    }

    const handleFile = e => { //
        setImages([...e.target.files]);
    }

    useEffect(() => {
        if (images.length < 1) return;
        const newImageUrls = [];
        images.forEach((image) => newImageUrls.push(URL.createObjectURL(image)));
        setImgURL(newImageUrls);
    }, [images])


    //  
    const today = new Date();
    const date = today.setDate(today.getDate());
    const defaultDateValue = new Date(date).toISOString().split("T")[0] // yyyy-mm-dd

    return (
        <Box sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            flexDirection: "column",
            height: "auto"
        }}>
            <Typography variant="h4" color="initial"></Typography>
            <Stack sx={{border: 1, borderColor: "grey.200", width: "60%", p: 2}} spacing={2}>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <InputLabel>:</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={houseType}
                            defaultValue={""}
                            sx={{width: 150}}
                            onChange={e => setHouseType(e.target.value)}
                        >
                            <MenuItem value={""} disabled>:</MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                        </Select>
                    </FormControl>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <InputLabel>:</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={roomType}
                            defaultValue={""}
                            sx={{width: 150}}
                            onChange={handleRoomType}

                        >
                            <MenuItem value={""} disabled>:</MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                        </Select>
                    </FormControl>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <InputLabel></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={addressCity}
                            defaultValue=""
                            sx={{width: 100}}
                            onChange={e => setAddressCity(e.target.value)}
                        >
                            <MenuItem value={""} disabled>:</MenuItem>
                            {CountryArray.map((Country, index) => {
                                return <MenuItem key={index} value={Country}>{Country}</MenuItem>
                            })}
                        </Select>
                    </FormControl>
                    <FormControl>
                        <InputLabel></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={addressArea}
                            defaultValue=""
                            sx={{width: 100}}
                            onChange={e => setAddressArea(e.target.value)}
                        >
                            <MenuItem value={""} disabled></MenuItem>
                            {cityData[addressCity] && cityData[addressCity].map((area, index) => {
                                return <MenuItem key={index} value={area["AreaName"]}>{area["AreaName"]}</MenuItem>
                            })}
                        </Select>
                    </FormControl>
                    <TextField
                        id="address"
                        label=""
                        sx={{pl: 1}}
                        value={Address}
                        onChange={e => setAddress(e.target.value)}
                    />
                </Stack>

                {floorStatus &&
                    <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                        <Typography variant="h6">:</Typography>
                        <TextField
                            id="floor"
                            label=""
                            sx={{pl: 1}}
                        />
                        
                        <TextField
                            id="room-id"
                            label=""
                            sx={{pl: 1, pr: 3}}
                        />
                        :
                        <TextField
                            id="all-floor-number"
                            label=""
                            sx={{pl: 1}}
                        />
                    </Stack>}

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <TextField
                        id="squareFeet"
                        label=""
                        sx={{pl: 1}}
                        value={squareFeet}
                        onChange={e => setSquareFeet(e.target.value)}
                    />
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <TextField
                        id="squareFeet"
                        label=""
                        sx={{pl: 1, width: 100}}
                        value={rooms[1].number}
                        onChange={e => handleSetRoom(e.target.value, "")}
                    />
                    <Typography variant="h6" sx={{pl: 1}}>:</Typography>
                    <TextField
                        id="squareFeet"
                        label=""
                        sx={{pl: 1, width: 100}}
                        value={rooms[2].number}
                        onChange={e => handleSetRoom(e.target.value, "")}
                    />
                    <Typography variant="h6" sx={{pl: 1}}>:</Typography>
                    <TextField
                        id="squareFeet"
                        label=""
                        sx={{pl: 1, width: 100}}
                        value={rooms[2].number}
                        onChange={e => handleSetRoom(e.target.value, "")}
                    />
                    <Button variant="contained" color="primary" sx={{ml: 5}}>
                        
                    </Button>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <TextField
                        id="rentMoney"
                        value={rentMoney}
                        onChange={e => setRentMoney(e.target.value)}
                        sx={{backgroundColor: "white"}}
                    />
                    <Typography variant="inherit" color="initial" sx={{pl: 3}}>:</Typography>
                    <FormControl>
                        <InputLabel></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            defaultValue={""}
                            value={deposit}
                            sx={{width: 150}}
                            onChange={e => setDeposit(e.target.value)}
                        >
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>

                        </Select>
                    </FormControl>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <InputLabel></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            defaultValue={""}
                            value={HouseForward}
                            sx={{width: 150}}
                            onChange={e => setHouseForward(e.target.value)}
                        >
                            <MenuItem value={""} disabled></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>

                        </Select>
                    </FormControl>
                </Stack>


                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    {rentMoneyHaveArray.map((Item) => {
                        return (
                            <FormControlLabel
                                key={Item.id}
                                label={Item.rentMoneyItem}
                                control={
                                    <Checkbox
                                        value=""
                                        checked={Item.status}
                                        onChange={() => handleChangeArray(Item.id, setRentMoneyHaveArray, rentMoneyHaveArray)}
                                        color="primary"
                                    />
                                }
                            />
                        )
                    })}

                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <TextField
                        id="ShortestRentTime"
                        label=""
                        value={ShortestRentTime}
                        onChange={e => setShortestRentTime(e.target.value)}
                        sx={{width: "12%"}}
                    />
                    
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    {IdentifyRequirement.map((Item) => {
                        return (
                            <FormControlLabel
                                key={Item.id}
                                label={Item.Identify}
                                control={
                                    <Checkbox
                                        value=""
                                        checked={Item.status}
                                        onChange={() => handleChangeArray(Item.id, setIdentifyRequirement, IdentifyRequirement)}
                                        color="primary"
                                    />
                                }
                            />
                        )
                    })}
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <RadioGroup row name="row-radio-buttons-group" value={SexRequirement}
                            onChange={e => setSexRequirement(e.target.value)}>
                            <FormControlLabel value="" control={<Radio/>} label=""/>
                            <FormControlLabel value="" control={<Radio/>} label=""/>
                            <FormControlLabel value="" control={<Radio/>} label=""/>
                        </RadioGroup>
                    </FormControl>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <RadioGroup row name="row-radio-buttons-group" value={ableToFire}
                            onChange={e => setAbleToFire(e.target.value)}>
                            <FormControlLabel value={true} control={<Radio/>} label=""/>
                            <FormControlLabel value={false} control={<Radio/>} label=""/>
                        </RadioGroup>
                    </FormControl>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <FormControl>
                        <RadioGroup row name="row-radio-buttons-group" value={AblePet}
                            onChange={e => setAblePet(e.target.value)}>
                            <FormControlLabel value={true} control={<Radio/>} label=""/>
                            <FormControlLabel value={false} control={<Radio/>} label=""/>
                        </RadioGroup>
                    </FormControl>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <input type="date" id="ableToGetInDate" name="ableToGetInDate" style={{height: 30}}
                        value={ableToGetInDate} onChange={e => setAbleToGetInDate(e.target.value)}/>
                    <Button variant="contained" color="primary" sx={{ml: 3}}
                        onClick={() => setAbleToGetInDate(defaultDateValue)}>
                        
                    </Button>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    {furnitureArray.map((Item) => {
                        return (
                            <FormControlLabel
                                key={Item.id}
                                label={Item.furniture}
                                control={
                                    <Checkbox
                                        value=""
                                        checked={Item.status}
                                        onChange={() => handleChangeArray(Item.id, setFurnitureArray, furnitureArray)}
                                        color="primary"
                                    />
                                }
                            />
                        )
                    })}
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    {ServiceArray.map((Item) => {
                        return (
                            <FormControlLabel
                                key={Item.id}
                                label={Item.Service}
                                control={
                                    <Checkbox
                                        value=""
                                        checked={Item.status}
                                        onChange={() => handleChangeArray(Item.id, setServiceArray, ServiceArray)}
                                        color="primary"
                                    />
                                }
                            />
                        )
                    })}
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    {PublicFacilityArray.map((Item) => {
                        return (
                            <FormControlLabel
                                key={Item.id}
                                label={Item.PublicFacility}
                                control={
                                    <Checkbox
                                        value=""
                                        checked={Item.status}
                                        onChange={() => handleChangeArray(Item.id, setPublicFacilityArray, PublicFacilityArray)}
                                        color="primary"
                                    />
                                }
                            />
                        )
                    })}
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6" sx={{pr: 5}}>:</Typography>
                    <TextField
                        required
                        id="House-title-required"
                        label=""
                        value={Title}
                        onChange={e => setTitle(e.target.value)}
                    />
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6" sx={{pr: 5}}>:</Typography>
                    <TextField
                        id="outlined-multiline-static"
                        label=""
                        multiline
                        rows={4}
                        value={Describe}
                        onChange={e => setDescribe(e.target.value)}
                        sx={{width: "70%"}}
                    />
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <Button
                        variant="contained"
                        component="label"
                    >
                        
                        <input
                            type="file"
                            hidden
                            onChange={handleFile}
                            multiple={true}
                        />
                    </Button>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center"}}> {/*  */}
                    <Typography variant="h6">:</Typography>
                    <Stack direction="row" spacing={2} sx={{flexWrap: "wrap"}}>
                        {ImgURL && ImgURL.map((url, i) => {
                            return <img key={i} src={url} width="475" height="450" alt={i}/>
                        })}
                    </Stack>
                </Stack>

                <Stack direction="row" sx={{alignItems: "center", justifyContent: "flex-end"}}> {/*  */}
                    <Button variant="contained" color="primary" size="large">
                        
                    </Button>
                    <Button variant="contained" sx={{backgroundColor: "#ab003c"}} size="large">
                        
                    </Button>
                </Stack>

            </Stack>
        </Box>
    );
}

export default AddRentPage;
