import * as React from "react";

import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { SetSearch } from "../../action";

import {
    Box,
    Tabs,
    Tab,
    Typography,
    Slider,
    FormControl,
    Checkbox,
    TextField,
    FormControlLabel,
    FormGroup
} from "@mui/material";

function ConditionOptions() {
    const searchData = useSelector(state => state.Search);
    const dispatch = useDispatch();

    const marks = [
        {
            value: 0,
            label: "0",
        },
        {
            value: 10000,
            label: "10,000",
        },
        {
            value: 20000,
            label: "20,000",
        },
        {
            value: 30000,
            label: "30,000",
        },
        {
            value: 40000,
            label: "40,000",
        },
    ];

    const handleChangeHouseType = (event, newValue) => { // 
        dispatch(SetSearch("setHouseType", newValue));
    }

    const handleChangGender = (event, newValue) => { // 
        dispatch(SetSearch("setGender", newValue));
    }

    const handleChangeRoomType = (event, newValue) => { // 
        dispatch(SetSearch("setRoomType", newValue));
        switch (newValue) {
        case "": {
            dispatch(SetSearch("setRoom", {...searchData.houseInfo.room, "": 1, "": 1, "": 1}));
            break;
        }
        case "": {
            dispatch(SetSearch("setRoom", {...searchData.houseInfo.room, "": 1, "": 1, "": 0}));
            break;
        }
        case "": {
            dispatch(SetSearch("setRoom", {...searchData.houseInfo.room, "": 1, "": 1, "": 1}));
            break;
        }
        case "": {
            dispatch(SetSearch("setRoom", {...searchData.houseInfo.room, "": 1, "": 0, "": 1}));
            break;
        }
        case "": {
            dispatch(SetSearch("setRoom", {...searchData.houseInfo.room, "": 1, "": 0, "": 0}));
            break;
        }
        default: {
            break;
        }
        }
    }

    const handleChangeRoom = (event, name) => { // 
        if (event.target.value >= 0) { dispatch(SetSearch("setRoom", {...searchData.houseInfo.room, [name]: parseInt(event.target.value)})); }
    }

    const handleChangeRent = (event, newValue) => { // 
        dispatch(SetSearch("setPriceRange", newValue));
    }

    const handleChangeTextRent = (event, index) => { // 
        const value = Array.from(searchData.priceRange);
        const newValue = parseInt(event.target.value);
        if (newValue >= 0) {
            value[index] = newValue; // 
            dispatch(SetSearch("setPriceRange", value));
        }
    };

    const handleCheckTextRent = () => { // 
        const [min, max] = Array.from(searchData.priceRange);
        if (min > max) {
            dispatch(SetSearch("setPriceRange", [max, min]));
        }
    };

    const rowProps = () => {
        return {
            sx: {
                mb: 1
            }
        }
    };

    const tabProps = (label, value) => { // Style
        return {
            "label": label,
            "value": value,
            "sx": {
                fontSize: "0.9rem",
                px: 1,
                py: 0,
                minHeight: "1.5rem",
                minWidth: "2rem",
                overflow: "default"
            }
        }
    }

    const numberProps = (sx={}) => { // {p: 2}
        return {
            size: "sm",
            type: "number",
            inputProps: {
                inputMode: "numeric",
                pattern: "[0-9]*"
            },
            InputLabelProps: { shrink: true },
            sx: {
                width: "6rem",
                mx: "0.2rem",
                ...sx
            }
        }
    }
    const roomProps = (name) => {
        return {
            label: name,
            value: searchData.houseInfo.room[name],
            disabled: searchData.houseInfo.roomType !== null
        }
    }

    const titleProps = (title) => {
        return {
            color: "rgba(0, 0, 0, 0.6)",
            children: title
        }
    }

    const isChecked = (name) => {
        const roles = new Set(searchData.equipmentAndServices.condition.role);
        return roles.has(name);
    }
    const [unlimited, setUnlimited] = useState(false);

    const handleChangeRole = (event) => {
        const checked = event.target.checked;
        const name = event.target.name;
        const roles = new Set(searchData.equipmentAndServices.condition.role);
        if (name === "") {
            if (!checked) {
                //
            } else {
                roles.clear();
            }
            setUnlimited(checked);
        } else {
            if (!checked) {
                roles.delete(name);
            } else {
                roles.add(name);
            }
            // setUnlimited(cityData[county].length === countyList.size);
        }
        dispatch(SetSearch("setRoles", Array.from(roles)));
    };
    return (
        <>
            <Box {...rowProps()}>
                <Typography {...titleProps("")} />
                <Box sx={{pl: 2, py: 1}}>
                    <Tabs value={searchData.houseInfo.houseType} onChange={handleChangeHouseType} sx={{minHeight: "0" }}>
                        <Tab {...tabProps("", null)} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                    </Tabs>
                </Box>
            </Box>
            <Box {...rowProps()}>
                <Typography {...titleProps("")} />
                <Box sx={{pl: 2, py: 1}}>
                    <Tabs value={searchData.houseInfo.roomType} onChange={handleChangeRoomType} sx={{minHeight: "0" }}>
                        <Tab {...tabProps("", null)} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                    </Tabs>
                </Box>
            </Box>
            <Box {...rowProps()}>
                <Typography {...titleProps("")} />
                <Box sx={{ display: "flex" ,pl: 2, py: 1}}>
                    <Box sx={{pt: 1}}>
                        <TextField
                            onChange={(e) => handleChangeRoom(e, "")}
                            {...numberProps()}
                            {...roomProps("")}
                        />
                        <TextField
                            onChange={(e) => handleChangeRoom(e, "")}
                            {...numberProps()}
                            {...roomProps("")}
                        />
                        <TextField
                            onChange={(e) => handleChangeRoom(e, "")}
                            {...numberProps()}
                            {...roomProps("")}
                        />
                    </Box>
                </Box>
            </Box>
            <Box {...rowProps()}>
                <Typography {...titleProps("")} />
                <Box sx={{pl: 2, py: 1}}>
                    <Box sx={{ width: "20rem", pt: 1 }}>
                        <Box>
                            <TextField
                                onChange={(e) => handleChangeTextRent(e, 0)}
                                onBlur={handleCheckTextRent}
                                label=''
                                value={searchData.priceRange[0]}
                                {...numberProps({width: "9.2rem"})}
                            />
                            <TextField
                                onChange={(e) => handleChangeTextRent(e, 1)}
                                onBlur={handleCheckTextRent}
                                label=''
                                value={searchData.priceRange[1]}
                                {...numberProps({width: "9.2rem"})}
                            />
                        </Box>
                        <Slider
                            sx={{ mx: "0.2rem", width: "18.4rem" }}
                            value={searchData.priceRange}
                            onChange={handleChangeRent}
                            min={0}
                            step={100}
                            max={40000}
                            marks={marks}
                            // disableSwap
                        />
                    </Box>
                </Box>
            </Box>
            <Box {...rowProps()}>
                <Typography {...titleProps("")} />
                <FormControl>
                    {/* <FormLabel id="county-group"></FormLabel> */}
                    <FormGroup
                        row
                        sx={{ pl: 2 }}
                    >
                        <FormControlLabel key={0} sx={{ "& .MuiFormControlLabel-label": { fontSize: "0.8rem", color: unlimited && "blue" }, "& .MuiSvgIcon-root": { fontSize: "0.8rem" }}}
                            control={<Checkbox checked={unlimited} onChange={handleChangeRole} name={""} />} label={""}/>
                        <FormControlLabel key={1} disabled={unlimited} sx={{"& .MuiFormControlLabel-label": { fontSize: "0.8rem", color: isChecked("") && "blue" }, "& .MuiSvgIcon-root": { fontSize: "0.8rem" }}}
                            control={<Checkbox checked={ isChecked("") } onChange={handleChangeRole} name={""} />} label={""}/>
                        <FormControlLabel key={2} disabled={unlimited} sx={{"& .MuiFormControlLabel-label": { fontSize: "0.8rem", color: isChecked("") && "blue" }, "& .MuiSvgIcon-root": { fontSize: "0.8rem" }}}
                            control={<Checkbox checked={ isChecked("") } onChange={handleChangeRole} name={""} />} label={""}/>
                        <FormControlLabel key={3} disabled={unlimited} sx={{"& .MuiFormControlLabel-label": { fontSize: "0.8rem", color: isChecked("") && "blue" }, "& .MuiSvgIcon-root": { fontSize: "0.8rem" }}}
                            control={<Checkbox checked={ isChecked("") } onChange={handleChangeRole} name={""} />} label={""}/>
                    </FormGroup>
                </FormControl>
            </Box>
            <Box {...rowProps()}>
                <Typography {...titleProps("")} />
                <Box sx={{pl: 2, py: 1}}>
                    <Tabs value={searchData.equipmentAndServices.condition.gender} onChange={handleChangGender} sx={{minHeight: "0" }}>
                        <Tab {...tabProps("", null)} />
                        <Tab {...tabProps("", "")} />
                        <Tab {...tabProps("", "")} />
                    </Tabs>
                </Box>
            </Box>
        </>
    )
}

export default ConditionOptions;
